/** @jsx jsx */
import { jsx } from '@emotion/core';
import { graphql } from 'gatsby';
import startCase from 'lodash/startCase';
import { Post } from 'models/post';
import PostsLayout from 'components/Layout/Posts';
import { Fragment } from 'react';
import SEO from 'components/SEO';

interface Props {
  data: {
    allMarkdownRemark: any;
    site: {
      siteMetadata: {
        title: string;
      };
    };
  };
  location: Location;
  pageContext: {
    lang: string;
  };
}

const CategoryTemplate = ({ data, location, pageContext: { lang } }: Props) => {
  const {
    allMarkdownRemark: { edges: posts, pageInfo },
  } = data;
  const [, categoryPath, categorySlug] = location.pathname.split('/');
  const path = `/${categoryPath}/${categorySlug}/`;

  return (
    <Fragment>
      <SEO title={startCase(categorySlug)} lang={lang} />
      <PostsLayout
        posts={posts.map((p: { node: Post }) => p.node)}
        pageInfo={{ ...pageInfo, defaultUrl: path, lang }}
        location={location}
      />
    </Fragment>
  );
};

export default CategoryTemplate;

export const pageQuery = graphql`
  query CategoryQuery($skip: Int!, $limit: Int!, $category: String!, $lang: String!) {
    allMarkdownRemark(
      skip: $skip
      limit: $limit
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { categories: { in: [$category] } }, fields: { lang: { eq: $lang } } }
    ) {
      edges {
        node {
          ...PostListQuery
        }
      }
      pageInfo {
        currentPage
        pageCount
      }
    }
  }
`;
